import  { Component }  from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';  
import { toCompleteEvent } from '../../actions/campaign';

class TimePage extends Component{ 

    componentDidMount(){

        const { condition} = this.props.rule;         
    
        if(condition.operator === "more-than-equal-to")
            setTimeout(_ => this.props.toCompleteEvent(this.props.campaignid, this.props.rule.id) , condition.value * 1000); 
    } 
    render(){    
        return null;
    }
}  

export default connect(null, { toCompleteEvent })(TimePage);


