import TYPES from '../static/Types';
import _ from 'lodash';
import utils from '../helpers/utils';

module.exports = {

    load_campaign: (url) => {

        return (dispatch) => {

            fetch(url)
                .then((response) => response.json())
                .then(json => {

                    try {

                        let rules = [];
                        let campaigns = [];
                        _.each(json.campaigns, campaign => {
                            rules[campaign.id] = (campaign.rules_g.rulesets);
                            campaigns[campaign.id] = campaign;
                        });

                        dispatch({
                            type: TYPES.LOAD_CAMP,
                            payload: { 
                                campaigns: json,
                                rulesets: rules
                            }
                        });
                        
                    } catch (err) {

                    }
                })
                .catch(() => {
                    dispatch({
                        type: TYPES.LOAD_CAMP_ERR,
                        payload: {
                            message: "Failed to load campaign"
                        }
                    });
                })
        }
    },

    toCompleteEvent: (campaignid, ruleid) => {

        return (dispatch) => {

            dispatch({
                type: TYPES.COMPLETE_EVENT,
                payload: { campaignid, ruleid }
            });
        }
    },

    addRuleSet: (campaignid, ruleset) => {

        return (dispatch) => {
            dispatch({
                type: TYPES.ADD_RULESET,
                payload: { campaignid, ruleset }
            });
        }
    },

    show: (id) => {
        return (dispatch) => {
            dispatch({
                type: TYPES.TOGGLE,
                payload: id
            })
        }
    },
    closeCampaign: (id, campaigns, type) => {  

        let campaign = _.find(campaigns,{id});

        if(campaign.options.close_cookie_duration !== false && type === 'hard')
            utils.setLoadedCampaign(id, campaign.options.close_cookie_duration);

        return (dispatch) => {
            dispatch({
                type: TYPES.EVENT_COMPLETED,
                payload: id
            })
        }
    }
}
 



