import { Component } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import { toCompleteEvent } from '../../actions/campaign';
import $ from 'jquery';

class ExitIntent extends Component {

    componentDidMount() {

        const { condition } = this.props.rule;

        switch (condition.operator) {
            case 'low':
                return this.addEvent.call(this, 0);
            case 'min':
                return this.addEvent.call(this, -50);
            case 'max':
                return this.addEvent.call(this, -150);
            default:
                break;
        }

    }

    addEvent(range) {

        $(document).on("mouseleave", e => {
            console.log(e);
            
            if (e.clientY <= range)
                this.props.toCompleteEvent(this.props.campaignid, this.props.rule.id);
        }, false);
    }

    render() {
        return null;
    }
}

export default connect(null, { toCompleteEvent })(ExitIntent);


