import React, { Component } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';

import { url } from './helpers/config';
import { load_campaign } from './actions/campaign';
import Campaign from './campaigns/campaign';
import Timer from './timer';

class App extends Component {

    constructor(props) {

        super(props);

        var script = document.currentScript || function () {
            var scripts = document.getElementsByTagName("script");
            return scripts[scripts.length - 1];
        }();

        // var account = script.getAttribute('data-account');
        var account = 1;
        var campaign = script.getAttribute('data-campaign');

        this.state = { account, campaign }
    }

    render() {

        const { campaigns } = this.props.campaign; 
        
        if (campaigns !== null) return <React.Fragment><Timer />{_.map(campaigns, (camp, k) => <Campaign key={k} camp={camp} />) }</React.Fragment>;

        return null;
    }

    componentDidMount() {

        const { account, campaign } = this.state;

        if (account || campaign) {

            // const campaign_url = (account) ? url.app + 'campaign/account/1' : url.app + 'campaign/' + campaign;
            this.props.load_campaign(url.mock_proxy);
        }
    }
}


const mapStateToProps = ({ campaign }) => {
    return {
        campaign
    }
}

export default connect(mapStateToProps, { load_campaign })(App);


