import React, { Component } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import { Engine } from 'json-rules-engine';

import utils from '../helpers/utils';
import Popper from '../popper';
import Rules from './rules';
import { addRuleSet, show } from '../actions/campaign';

class Campaign extends Component {

    isValid(rules) {
        return true;
    }

    buildRuleSet() {

        const { camp: campaign } = this.props;

        const rules = (campaign.rules_g.rules);

        let rulesSet = [];

        _.each(rules, rule => {
            rulesSet[rule.id] = false
        });
        this.props.addRuleSet(campaign.id, rulesSet);
    }

    componentDidMount() {
        this.buildRuleSet.call(this);
    }

    handleLoader() {
        const { camp: campaign } = this.props;

        if (!this.isValid(campaign.rules_g))
            return null;

        return (<Popper type={campaign.type} id={campaign.id} >
            <div dangerouslySetInnerHTML={{ __html: this.props.camp.html }} ></div>
            <Rules rules={campaign.rules_g.rules} wrapperid={12} campaignid={campaign.id} />
        </Popper>);
    }

    compileRuleSet(ruleset) {

        _.each(ruleset, (rules, campaignid) => {

            if (_.includes(utils.getLoadedCampaigns(), campaignid) === false && _.includes(this.props.loadedcampaign, campaignid) === false) {

                let engine = new Engine();

                engine.addRule({
                    conditions: { ...this.props.ruleSets[campaignid] },
                    event: {
                        type: 'success',
                        params: {
                            message: 'start'
                        }
                    }
                });

                engine
                    .run({ ...rules })
                    .then(events => {
                        _.map(events, event => {
                            if (event.params.message === "start") {
                                this.props.show(campaignid);
                            }
                        }
                        );

                    });
            }

        });
    }

    componentWillUpdate(prevProps, prevState) {

        if (prevProps.rulesState !== this.props.rulesState)
            this.compileRuleSet.call(this, { ...prevProps.rulesState });
    }

    render() {
        return this.handleLoader.call(this);
    }
}

const mapStateToProps = ({ campaign }) => {
    return {
        rulesState: campaign.rulesState,
        ruleSets: campaign.ruleSets,
        loadedcampaign: campaign.loadedCampaign
    }
}

export default connect(mapStateToProps, { addRuleSet, show })(Campaign);


