import _ from 'lodash';
import Cookies from 'js-cookie';

const item = {
    prefix: 'cr-'
}

module.exports = {

    setCookie(name, value, time) {
        Cookies.set(name, value, { expires: time, path: '' })
    },

    getCookie(name) {
        return Cookies.get(name);
    },

    getAllCookies() {
        return Cookies.get();
    },

    eraseCookie(name) {
        Cookies.remove(name);
    },

    getLoadedCampaign(id) {
        return this.getCookie(item.prefix + id)
    },

    getLoadedCampaigns() {

        let campaigns = this.getAllCookies();

        let loadedCampagin = _.reduce(campaigns, (result, value, key) => {

            if (key.includes(item.prefix) === true && value === "true")
                result.push(key.replace('cr-', ''));

            return result;
        }, []);

        return loadedCampagin;
    },

    setLoadedCampaign(id, time) {

        this.setCookie(item.prefix + id, true, time);
    },

    eraseLoadedCampaign() {
        this.eraseCookie(item.loadedCampagin);
    }
}


