class UrlCore {


}

class Url extends UrlCore {

    constructor(location) {
        super()
        this.location = location;
    }

    getUrl() {
        return this.location;
    }

    isTheHomepage() {

        
    }

    isNotTheHomepage() {

    }

    exactlyMatches(value) {
        
        var regExp = new RegExp('^'+value+'$');

        return regExp.test(this.location.href);
    }

    doesExactlyMatch(value) { 
        return !this.exactlyMatches(value);
    }

    contains(value) {

        if (this.location.href.indexOf(value) > -1) 
            return true;
            
        return false;
    }

    doesNotContain(value) {
        return !this.contains(value);
    }

    endWith(value) {
        return this.location.href.endsWith(value)
    }

    doesNotEndWith(value) {
        return !this.endWith(value)
    }

    matchesThepattern() {

    }
}

export default Url;


