import React, { Component } from 'react';
import Modal from './modal';
import PopperRight from './popperRight';
import PopperTop from './popperTop';

class Popper extends Component{

    getPopper(type){
        switch(type){
            case 'modal':
                return Modal;
            case 'pop-right':
                return PopperRight;
            case 'pop-top':
                return PopperTop;
            default:
                return _ => null;
        }
    }

    render(){

        const Popper = this.getPopper(this.props.type);

        return (
            <Popper  {...this.props}>
                {this.props.children}
            </Popper>
        )
    }
}

export default Popper;


